import { driver } from "driver.js";
import "driver.js/dist/driver.css";
import store from "../store";
import { TOGGLE_ADD_GALLERY } from "../reducer/keys";
import Swal from "sweetalert2";

class appTour {
    constructor(navigate, high = false) {
        this.tour = new driver({
            allowClose: high,
            stagePadding: 0,
            popoverOffset: 20,
            smoothScroll: true,
            onDestroyStarted: () => {
                if (localStorage.getItem('tourStep') == '2') {
                    localStorage.setItem('tourStep', 3)
                    this.stopTour()
                }
            },
            steps: [{
                element: '#tour_1',
                popover: {
                    title: 'Create Gallery',
                    description: 'Creating a new gallery is the first step to curating content and turning it shoppable with Tagshop.',
                    showButtons: ['next'],
                    nextBtnText: 'Create',
                    onNextClick: () => {
                        localStorage.setItem('tourStep', 2)
                        store.dispatch({ type: TOGGLE_ADD_GALLERY, payload: true });
                        this.stopTour()
                    }
                }
            },
            {
                element: '#create_feed',
                popover: {
                    title: 'Create a Feed',
                    description: 'Creating a feed will aggregate social content from your chosen network and source in a curated content stream.',
                    showButtons: ['next'],
                    nextBtnText: 'OK',
                    onNextClick: () => {
                        localStorage.setItem('tourStep', 4)
                        this.stopTour()
                    }
                }
            },
            {
                element: '#pd_ctlog',
                popover: {
                    title: 'Sync Products',
                    description: 'Sync product catalog with Tagshop to list essential product details and help buyers make informed purchases.',
                    showButtons: ['next', 'previous'],
                    nextBtnText: 'Next',
                    prevBtnText: 'End Tour',
                    onNextClick: () => {
                        localStorage.setItem('tourStep', 5)
                        navigate('/content/moderation')
                        this.stopTour()
                    },
                    onPrevClick: this.resetTour
                }
            },
            {
                element: '#tag_pd',
                popover: {
                    title: 'Tag products with UGC',
                    description: 'Turn the aggregated posts shoppable by adding relevant product tags from the synced product catalog.',
                    showButtons: ['next', 'previous'],
                    nextBtnText: 'Next',
                    prevBtnText: 'End Tour',
                    onNextClick: () => {
                        localStorage.setItem('tourStep', 6)
                        navigate('/website/publish')
                        this.nextStep()
                    },
                    onPrevClick: this.resetTour
                }
            },
            {
                element: '#publish',
                popover: {
                    title: 'Publish Your Widget',
                    description: 'Publish your shoppable gallery on your desired touchpoints - homepage, product detail pages, or in-emails.',
                    showButtons: ['next', 'previous'],
                    nextBtnText: 'Next',
                    prevBtnText: 'End Tour',
                    onNextClick: () => {
                        localStorage.setItem('tourStep', 7)
                        navigate('/autopilot')
                        this.nextStep()
                    },
                    onPrevClick: this.resetTour
                }
            },
            {
                element: '#auto_pilot',
                popover: {
                    title: 'Explore Autopilot',
                    description: 'Moderate your curated gallery further with the help of AI to remove irrelevant or duplicate posts instantly.',
                    showButtons: ['next'],
                    nextBtnText: 'Next',
                    onNextClick: () => {
                        this.stopTour()
                        this.tourComplete()
                    }
                }
            }
            ]
        })
    }

    stopTour = () => this.tour.destroy()
    nextStep = () => this.tour.moveNext()
    resetTour = () => {
        localStorage.removeItem("tourStep");
        localStorage.setItem("isTour",false);
        this.stopTour();
    };
    tourComplete = () => {
        Swal.fire({
            title: `Tour Complete`,
            icon: 'success',
            text: `Youâ€™re all set! If you need help, feel free to contact us at support@tagshop.ai Happy tagging!`,
            showCancelButton: false,
            confirmButtonText: "Done",
            confirmButtonColor:`var(--bs-success)`,
            allowOutsideClick:false

        }).then((result) => {
            localStorage.removeItem('tourStep')
            localStorage.setItem("isTour",false);
        });
    }
}

export default appTour